<template>
    <div class="textarea-block" :class="className">
        <textarea
            :id="id"
            class="textarea-block__textarea"
            placeholder=" "
            v-model="textareaMessage"
            @keyup="handleInput"
            required
        ></textarea>
        <label :for="id" class="textarea-block__label">{{ label }}</label>
        <div
            class="textarea-block__email-error"
            v-if="showError"
        >
            {{ errorMessage }}
        </div>
        <slot></slot>
    </div>
</template>

<script>
export default {
    props: {
        className: String,
        id: {
            type: [String, Number],
            required: true
        },
        label: {
            type: String,
            required: true
        },
        showError: {
            type: Boolean,
            default: false
        },
        errorMessage: String,
        value: {
            type: String,
            default: ''
        }
    },
    data () {
        return {
            textareaMessage: this.value
        }
    },
    methods: {
        handleInput: function() {
            this.$emit('on-input', this.textareaMessage)
        }
    }
}
</script>

