        padding: 15px 0;
        color: var(--color-mid-silver);
        font-size: var(--default-font-size);
        line-height: var(--default-line-height);
        width: 100%;
        transition: border-color var(--tr-xshort) ease;
        min-width: 100%;
        max-width: 100%;
        max-height: 200px;
        min-height: 135px;

        &:focus {
            outline: none;
            border-color: var(--color-cerulean);
        }

        &:focus ~ .textarea-block__label,
        &:not(:placeholder-shown) ~ .textarea-block__label {
            top: -11px;
