<template>
  	<button
		class="btn"
		:class="`btn_${type} ${className}`"
		:disabled="disabled"
		@click="clickHandler"
	>
      {{ text }}
  </button>
</template>

<script>
export default {
	props: {
		className: {
			type: String,
			default: ''
		},
		disabled: {
			type: Boolean
		},
		text: {
			type: String,
			required: true
		},
		type: {
			type: String,
			default: 'blue'
		}
	},
	methods: {
		clickHandler: function() {
			this.$emit('on-click')
		}
	}
}
</script>

<style lang="scss" scoped>
.btn {
	display: block;
	position: relative;
	@extend %f-cy;
	height: 50px;
	padding: 0 24px;
	border-radius: 25px;
	font-size: var(--default-font-size);
	white-space: nowrap;
	text-align: center;
    transition: color var(--tr-xshort) ease-in-out, background var(--tr-xshort) ease-in-out;
	cursor: pointer;

	&:disabled {
		cursor: default;
	}

	&_blue {
		color: var(--color-white);
		border: 2px solid var(--color-cerulean);
		background: var(--color-cerulean);

		&:hover {
			color: var(--color-cerulean);
			background: transparent;
		}
		
		&:disabled {
			background-color: var(--color-silver);
			color: var(--color-default);
			border-color: var(--color-silver);
		}
	}

	&_transparent {
		color: var(--color-cerulean);
		background-color: transparent;
		border: none;

		&:disabled {
			color: var(--color-default);
		}
	}
}
</style>